<template>
  <div class="var-container">
    <label v-if="label" class="fl">{{label}}</label>
    <el-cascader
      v-model="selectedOptions"
      popper-class="var-cascader"
      filterable
      :options="options"
      :show-all-levels="false"
      placeholder="请选择变量"
      @change="handleChange"
    ></el-cascader>
  </div>
</template>

<script>
import {queryVarAutoSplit} from '@/api/analysis.js';
import {initVarListSetting} from '@/assets/scripts/getSomeSetting.js';
export default {
  name: 'var-select',
  props: {
    label: {
      type: String
    },
    initVar: {
      typeof: Array,
      default: () => []
    },
    varList: {
      typeof: Array,
      default: () => []
    }
  },
  data() {
    return {
      selectedOptions: this.initVar
    };
  },
  computed: {
    options() {
      const initVarList = initVarListSetting();
      if (this.varList.length) {
        for (let item of this.varList) {
          if (item.transmittype === '1') {
            initVarList[0].children.push(item);
          } else if (item.transmittype === '0') {
            initVarList[1].children.push(item);
          } else if (item.transmittype === '2') {
            initVarList[2].children.push(item);
          }
        }
      }
      return initVarList;
    }
  },
  watch: {
    initVar: {
      handler: function(newV) {
        // 延时进行，有可能varList和initVar同时改变，导致varList后执行，对selectedOptions重置了
        this.$nextTick(() => {
          this.selectedOptions = newV;
        });
      },
      immediate: true
    }
  },
  created() {},
  mounted() {},
  methods: {
    handleChange(val = []) {
      console.log(val);
      this.$emit('update:selected', val);
    }
  }
};
</script>


<style scoped>
.var-container {
  position: relative;
  display: inline-block;
  height: 40px;
}
.el-cascader {
  width: 220px;
}
label {
  display: inline-block;
  width: 100px;
  padding-top: 14px;
}
</style>
