<template>
  <div class="sample-chart-container">
    <div id="sample-chart" v-show="showChart"></div>
    <!-- <div v-show="showChart" class="data-source">
      <span>蜂鸟</span>
    </div>-->
  </div>
</template>

<script>
import {getColorObj} from '@/assets/scripts/getSomeSetting.js';
import {yAxisFormatter} from '@/assets/scripts/utils.js';
import {getSampleMonitorData} from '@/api/monitor.js';
import {getSampleHistoryList} from '@/api/sample.js';

const keyToName = {
  count: '总数',
  t10: 'T10指标逾期率',
  resultrate: '通过率'
};
const allColorObj = getColorObj();
const colorArr = Object.values(keyToName).map(key => allColorObj[key]);

export default {
  name: 'sample-chart',
  data() {
    return {
      showChart: false
    };
  },
  mounted() {
    this.$on('drawSampleChart', samplename => {
      this.drawFunc(samplename);
    });
  },
  methods: {
    drawFunc(samplename) {
      this.showChart = true;
      this.getData(samplename)
        .then(result => {
          if (Array.isArray(result.result)) {
            if (!this.chart) {
              const elem = this.$el.querySelector('#sample-chart');
              this.chart = echarts.init(elem);
            }
            const option = this.getOption(result.result);
            this.chart.setOption(option);
          }
        })
        .catch(err => {
          console.log(err);
          this.showChart = false;
        });
    },
    getData(samplename) {
      return getSampleMonitorData(samplename);
    },
    getOption(data = []) {
      const xData = data.map(item => item.time);
      const yArr = Object.keys(keyToName).map(name =>
        data.map(item => {
          let num = item[name];
          if (typeof num === 'number') {
            num = Number.isInteger(num) ? num : num.toFixed(2);
          }
          return num;
        })
      );
      const seriesData = Object.values(keyToName).map((name, i) => {
        return {
          name: `${name}`,
          type: 'line',
          yAxisIndex: i,
          smooth: true,
          data: yArr[i]
        };
      });
      const options = {
        title: {
          text: '选择样本参考图'
        },
        color: colorArr,
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'line'
          },
          formatter: params => {
            const arr = params.map(item => {
              return `${item.marker} ${item.seriesName}：${item.data}${item.seriesIndex < 1 ? '人' : '%'}`;
            });
            arr.unshift(params[0].name);
            return arr.join('<br />');
          }
        },
        legend: {
          top: 0,
          data: Object.values(keyToName)
        },
        grid: {
          left: '10%',
          top: '20%',
          right: '130px',
          bottom: '20%'
        },
        xAxis: [
          {
            type: 'category',
            data: xData,
            boundaryGap: false
          }
        ],
        yAxis: [
          {
            scale: true,
            splitArea: {
              show: true
            },
            name: '总数',
            nameGap: 10,
            min: 0,
            position: 'left',
            axisLine: {
              lineStyle: {
                color: colorArr[0]
              }
            },
            axisLabel: {
              formatter: yAxisFormatter
            },
            boundaryGap: [0.2, 0.2]
          },
          {
            scale: true,
            splitArea: {
              show: true
            },
            name: 'T10指标逾期率（%）',
            nameGap: 10,
            min: 0,
            position: 'right',
            axisLine: {
              lineStyle: {
                color: colorArr[1]
              }
            },
            boundaryGap: [0.2, 0.2]
          },
          {
            scale: true,
            splitArea: {
              show: true
            },
            name: '通过率（%）',
            nameGap: 10,
            min: 0,
            max: 100,
            position: 'right',
            offset: 90,
            axisLine: {
              lineStyle: {
                color: colorArr[2]
              }
            },
            boundaryGap: [0.2, 0.2]
          }
        ],
        series: seriesData
      };
      if (xData.length > 30) {
        options.dataZoom = [
          {
            type: 'slider',
            y: '90%',
            start: 0,
            end: 100
          }
        ];
      }
      return options;
    }
  }
};
</script>

<style scoped>
#sample-chart {
  position: relative;
  left: 0;
  right: 0;
  width: 100%;
  height: 300px;
  background: rgba(211, 211, 211, 0.5);
}
</style>
