<template>
  <div class="datepicker-container flex-wrap">
    <label v-if="label">{{ label }}</label>
    <div class="el-button" v-show="!showDataPicker">
      <input class="placeholder" type="text" :value="text" readonly @focus.stop="toggleDataPicker">
    </div>
    <el-date-picker
      ref="datePicker"
      :editable="false"
      v-model="dates"
      v-show="showDataPicker"
      type="daterange"
      unlink-panels
      @change="datesChangeFunc"
      value-format="yyyy-MM-dd"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
      :picker-options="pickerOptions"
    ></el-date-picker>
  </div>
</template>

<script>
import dayjs from 'dayjs';
export default {
  name: '',
  props: {
    label: {
      type: String
    },
    initDates: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now() || time.getTime() < new Date('2015/07/01').getTime();
        },
        shortcuts: [
          {
            text: '最近一周',
            onClick(picker) {
              const end = dayjs().format('YYYY-MM-DD');
              const start = dayjs()
                .add(-6, 'DAY')
                .format('YYYY-MM-DD');
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一个月',
            onClick(picker) {
              const end = dayjs().format('YYYY-MM-DD');
              const start = dayjs()
                .add(-1, 'month')
                .format('YYYY-MM-DD');
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近三个月',
            onClick(picker) {
              const end = dayjs().format('YYYY-MM-DD');
              const start = dayjs()
                .add(-3, 'month')
                .format('YYYY-MM-DD');
              picker.$emit('pick', [start, end]);
            }
          }
        ]
      },
      dates: this.initDates,
      text: '',
      showDataPicker: true
    };
  },
  watch: {
    initDates: {
      handler: function(newV = []) {
        this.dates = newV;
        this.showDataPicker = true;
        if (this.dates[1] === dayjs().format('YYYY-MM-DD')) {
          if (
            this.dates[0] ===
            dayjs()
              .add(-3, 'month')
              .format('YYYY-MM-DD')
          ) {
            this.showDataPicker = false;
            this.text = '近三个月';
          } else if (
            this.dates[0] ===
            dayjs()
              .add(-1, 'month')
              .format('YYYY-MM-DD')
          ) {
            this.showDataPicker = false;
            this.text = '近一个月';
          } else if (
            this.dates[0] ===
            dayjs()
              .add(-6, 'DAY')
              .format('YYYY-MM-DD')
          ) {
            this.showDataPicker = false;
            this.text = '最近一周';
          }
        }
      },
      immediate: true
    }
  },
  methods: {
    datesChangeFunc(values) {
      values = values || [];
      this.dates = values;
      this.$emit('update:date', values);
    },
    toggleDataPicker() {
      this.showDataPicker = true;
      this.$refs.datePicker.focus();
    }
  }
};
</script>

<style scoped>
label {
  display: block;
  width: 100px;
  padding-top: 14px;
}
.el-button {
  position: relative;
  height: 40px;
  width: 170px;
}
.placeholder {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  border: 0;
  /* width: 100%; */
  text-align: center;
  background: transparent;
  padding: 0;
  color: #696969;
  line-height: 14px;
  font-size: 14px;
}
</style>
