<template>
  <div class="sample-container flex-wrap">
    <label v-if="label">{{ label }}</label>
    <div>
      <el-input
        class="sample-input"
        suffix-icon="el-icon-arrow-down"
        placeholder="请选择样本名称"
        @blur="inputBlurFunc"
        @input="filterOptions"
      ></el-input>
      <el-cascader
        v-model="selectedOptions"
        popper-class="sample-cascader"
        :options="curOptions"
        :show-all-levels="false"
        @active-item-change="getSampleList"
        @change="handleChange"
        @focus="cascaderFocusFunc"
      ></el-cascader>
    </div>
  </div>
</template>

<script>
import {getSampleListByProduct} from '@/api/sample.js';
export default {
  name: '',
  props: {
    label: {
      type: String
    },
    initSample: {
      typeof: Array,
      default: () => []
    }
  },
  data() {
    return {
      selectedOptions: this.initSample,
      options: [],
      curOptions: []
    };
  },
  computed: {
    rcproductList() {
      return this.$store.getters.rcproductList();
    }
  },
  watch: {
    initSample(newV) {
      // 可能getRCrcproductList()还未返回数据，this.options为[];
      if (this.options.length) {
        this.getSampleList(newV.slice(0, 2), true, newV);
      } else {
        setTimeout(() => {
          this.getSampleList(newV.slice(0, 2), true, newV);
        }, 500);
      }
    },
    rcproductList: function(newV = '') {
      if (newV.length) {
        this.createCascaderData();
      }
    }
  },
  created() {
    this.createCascaderData();
  },
  mounted() {
    this.cascader = this.$el.querySelector('.sample-container .el-cascader');
    this.input = this.$el.querySelector('.sample-input input');
  },
  methods: {
    getModuleTypeList() {
      return [
        {
          value: '模块类型',
          label: '模块类型',
          disabled: true
        },
        {
          value: '0',
          label: '授信',
          children: []
        },
        {
          value: '1',
          label: '前置',
          children: []
        },
        {
          value: '2',
          label: '评分卡',
          children: []
        }
      ];
    },
    createCascaderData() {
      this.options = this.rcproductList.map(item => {
        return {
          value: item.rcProductName,
          label: item.rcProductName,
          children: this.getModuleTypeList()
        };
      });
      this.options.unshift({
        value: '样本名称',
        label: '样本名称',
        disabled: true
      });
      this.curOptions = this.options;
      // 从历史记录进来时，initSample可能已有值
      if (this.initSample.length) {
        this.getSampleList(this.initSample.slice(0, 2), true, this.initSample);
      }
    },
    getSampleList(val, isInit = false, initVal = []) {
      if (val.length !== 2) {
        return;
      }
      let theOne = null;
      for (let i = 0; i < this.options.length; i++) {
        if (this.options[i].value === val[0]) {
          for (let j = 0; j < this.options[i].children.length; j++) {
            if (this.options[i].children[j].value === val[1]) {
              theOne = this.options[i].children[j];
              break;
            }
          }
          break;
        }
      }
      if ((theOne && theOne.children && theOne.children.length) || theOne === null) {
        return;
      }
      getSampleListByProduct(val[0], Number(val[1]))
        .then(result => {
          if (Array.isArray(result.result)) {
            theOne.children = result.result.map(item => {
              return {
                label: item.modulename,
                value: item.modulename
              };
            });
            if (!theOne.children.length) {
              theOne.children.push({
                label: '暂无数据',
                value: '暂无数据',
                disabled: true
              });
            }
            if (isInit) {
              this.selectedOptions = initVal;
            }
            return;
          }
        })
        .catch(err => console.log(err));
    },
    handleChange(values) {
      values = values || [];
      this.$emit('update:selected', values);
      this.$nextTick(() => {
        this.inputBlurFunc();
      });
    },
    filterOptions(val) {
      this.curOptions = this.options.filter(item => !item.disabled && item.label.indexOf(val) > -1);
    },
    inputBlurFunc() {
      this.cascader.style.zIndex = 2;
      this.cascader.style.opacity = 1;
    },
    cascaderFocusFunc() {
      this.cascader.style.zIndex = 0;
      this.cascader.style.opacity = 0;
      this.input.focus();
    }
  }
};
</script>


<style scoped>
.sample-container > div {
  position: relative;
  height: 40px;
}
.sample-container > div,
.el-cascader,
.sample-input {
  width: 220px;
}
label {
  display: block;
  width: 100px;
  padding-top: 14px;
}
.sample-input {
  position: relative;
  z-index: 1;
}
.el-cascader {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  opacity: 1;
  height: 40px;
}
</style>
